import { BasicUserInterface } from "./user";

export interface BasicProfileInterface extends BasicUserInterface {
    lastName: string;
    phoneNumbers: number[];
    organisation: string;
    roles: string[];
    proUrl: string;
    isSecurity: boolean;
}

export const createEmptyProfile = (): BasicProfileInterface => ({
    displayName: "",
    emails: [],
    lastName: "",
    organisation: "",
    phoneNumbers: [],
    proUrl: "",
    roles: [],
    username: "",
    isSecurity: false
});
